import { useState, ChangeEvent, FormEvent } from "react";
import emailjs from "@emailjs/browser";
import { primaryEmail, primaryPhone } from "@/constants";
import Image from "next/image";
import Link from "next/link";
import { Mail, MapPin, Phone } from "lucide-react";

type FormData = {
	name: string;
	email: string;
	phone: string;
	message: string;
};

export default function Contact721() {
	const [formData, setFormData] = useState<FormData>({
		name: "",
		email: "",
		phone: "",
		message: "",
	});

	const [showToast, setShowToast] = useState(false);

	const handleChange = (
		e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
	) => {
		const { name, value } = e.target;
		setFormData((prevData) => ({
			...prevData,
			[name]: value,
		}));
	};

	const handleSubmit = (e: FormEvent) => {
		e.preventDefault();

		formData.message = formData.message.concat(`\n\nFrom: ${formData.email}`);

		emailjs
			.send(
				process.env.NEXT_PUBLIC_EMAILJS_SERVICE_ID!,
				process.env.NEXT_PUBLIC_EMAILJS_TEMPLATE_ID!,
				formData,
				process.env.NEXT_PUBLIC_EMAILJS_PUBLIC_KEY
			)
			.then(() => {
				setShowToast(true);
				setFormData({ name: "", email: "", phone: "", message: "" });
				setTimeout(() => setShowToast(false), 3000);
			})
			.catch((error) => {
				console.error("EmailJS error:", error);
			});
		setShowToast(true);
		setTimeout(() => setShowToast(false), 10000);

		setFormData({ name: "", email: "", phone: "", message: "" });
	};
	return (
		<div>
			<section id="ContactForm-721">
				<div className="cs-container">
					<div className="cs-picture">
						<Image
							src="/images/contact-image.jpg"
							alt="runner"
							width={610}
							height={425}
						/>
					</div>
					<div className="cs-content">
						<span className="cs-topper">Contact Us</span>
						<h2 className="cs-title">Get in Touch</h2>
						<p className="cs-text">
							Questions about Red Barn Market? Feel free to reach out!
						</p>
						{/*Form*/}
						<form
							className="cs-form"
							id="cs-form-718-721"
							name="Contact Form"
							method="post"
							onSubmit={handleSubmit}
						>
							<label className="cs-label">
								Name
								<input
									className="cs-input"
									required
									type="text"
									id="name-718-721"
									name="name"
									placeholder="Name"
									value={formData.name}
									onChange={handleChange}
								/>
							</label>
							<label className="cs-label cs-email">
								Email
								<input
									className="cs-input"
									required
									type="text"
									id="email-718-721"
									name="email"
									placeholder="Email"
									value={formData.email}
									onChange={handleChange}
								/>
							</label>
							<label className="cs-label cs-phone">
								Phone
								<input
									className="cs-input"
									required
									type="text"
									id="phone-718-721"
									name="phone"
									placeholder="Phone"
									onChange={handleChange}
									value={formData.phone}
								/>
							</label>
							<label className="cs-label">
								Message
								<textarea
									className="cs-input cs-textarea"
									required
									name="message"
									id="message-718-721"
									placeholder="Write message..."
									onChange={handleChange}
									value={formData.message}
								></textarea>
							</label>
							{showToast && (
								<div className="toast">Thanks for reaching out!</div>
							)}

							<button className="cs-button-solid cs-submit" type="submit">
								Submit
							</button>
						</form>
					</div>
				</div>
			</section>

			{/* No headers, so no section tag */}
			<div id="ContactStrip-721">
				<ul className="cs-stat-group">
					<li className="cs-item">
						<div className="cs-picture">
							<Phone color="#fff" size={32} />
						</div>
						<div className="cs-flex-group">
							<span className="cs-header">Phone</span>
							<Link href={`tel:${primaryPhone}`} className="cs-link">
								{primaryPhone}
							</Link>
						</div>
					</li>
					<li className="cs-item">
						<div className="cs-picture">
							<Mail color="#fff" size={32} />
						</div>
						<div className="cs-flex-group">
							<span className="cs-header">Email</span>
							<Link href={`mailto:${primaryEmail}`} className="cs-link">
								{primaryEmail}
							</Link>
						</div>
					</li>
					<li className="cs-item">
						<div className="cs-picture">
							<MapPin color="#fff" size={32} />
						</div>
						<div className="cs-flex-group">
							<span className="cs-header">Address</span>
							<span className="cs-address">
								Northwest Washington Fairgrounds
							</span>
						</div>
					</li>
				</ul>
			</div>

			{/* ============================================ */}
			{/*                    Map                       */}
			{/* ============================================ */}

			<section id="ContactMap-721">
				<iframe
					src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2620.8163532922786!2d-122.47680991352235!3d48.937939605230504!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x5485b70c58a2d96d%3A0xc198c24920b1da1e!2sWECU%20Expo%20Building!5e0!3m2!1sen!2sus!4v1758562033336!5m2!1sen!2sus"
					width="100%"
					height="540"
					style={{ border: 0 }}
					allowFullScreen
					loading="lazy"
					referrerPolicy="no-referrer-when-downgrade"
				></iframe>
			</section>
		</div>
	);
}
